function convertToPascal(name: string) {
  const converter = (matches: string, ...args: any[]) => {
    return matches[1].toUpperCase();
  };

  const result = name.replace(/(\-\w)/g, converter);
  return result.charAt(0).toUpperCase() + result.slice(1);
}

